export default {
	data() {
		return {
			title: ["文章", "作者"],
			nowIndex: 0,
			newsData: []
		};
	},
	onReady() {
		this.getData()
	},
	methods: {
		setContent(e) {
			this.nowIndex = e.target.dataset.id
		},
		getData(Cname) {
			uni.showLoading({
				title: "努力加载中..."
			})
			uniCloud.callFunction({
				name: "test",
				data: {
					newClass: Cname
				},
				data: {
					Email: "2993863530@qq.com",
					password: "123456789"
				},
				success: (res) => {
					this.newsData = res.result.attention
					uni.hideLoading()
				},
				fail: (err) => {
					console.log(err);
					uni.hideLoading()
				}
			})
		}
	}
}
